:root {
  --bg-color-primery: #191919;
  --color-text-primery: rgba(255, 255, 255, .8);
  --color-title: #e7e7e7;
  --color-btn: rgba(255, 255, 255, .9);
  --bg-btn-hero: #df3d3a;
  --bg-btn-hover-hero: #b5302d;
  --time-duration: 350ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.feedback__title, .sales__title, .questions__title, .review__title, .brandhistory__title, .garanties__title, .features__title, .promo__title, .benefits__title, .design__title {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: var(--color-title);
  margin-bottom: 32px;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .feedback__title, .sales__title, .questions__title, .review__title, .brandhistory__title, .garanties__title, .features__title, .promo__title, .benefits__title, .design__title {
    font-size: 30px;
  }
}

@media screen and (min-width: 1440px) {
  .feedback__title, .sales__title, .questions__title, .review__title, .brandhistory__title, .garanties__title, .features__title, .promo__title, .benefits__title, .design__title {
    margin-bottom: 50px;
    font-size: 54px;
  }
}

.questions__subtitle, .garanties__subtitle, .features__subtitle, .promo__subtitle, .design__subtitle {
  color: var(--color-title);
  font-size: 20px;
  font-weight: 600;
  line-height: 1.25;
}

@media screen and (min-width: 1440px) {
  .questions__subtitle, .garanties__subtitle, .features__subtitle, .promo__subtitle, .design__subtitle {
    font-size: 28px;
  }
}

.sales__btn, .hero__btn {
  background-color: var(--bg-btn-hero);
  color: var(--color-btn);
  letter-spacing: .02em;
  text-transform: uppercase;
  cursor: pointer;
  background-size: 200% 100%;
  background-image: linear-gradient(to right, var(--bg-btn-hero) 50%, var(--bg-btn-hover-hero) 50%);
  padding: 19px 96px 20px 97px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.2;
  transition: background-position .3s cubic-bezier(.19, 1, .22, 1) .1s, color .5s, background-color .5s;
}

@media screen and (min-width: 768px) {
  .sales__btn, .hero__btn {
    padding: 19px 103px 20px 104px;
  }
}

@media screen and (min-width: 1440px) {
  .sales__btn, .hero__btn {
    padding: 22px 95px;
    font-size: 20px;
  }
}

.sales__btn:hover, .hero__btn:hover, .sales__btn:focus, .hero__btn:focus {
  color: var(--color-btn);
  background-color: var(--bg-btn-hover-hero);
  background-position: -100% 100%;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

